@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global body styles */
body, html, #root {
  @apply h-screen w-screen m-0 p-0 font-sans text-base text-gray-900;
  background: url("../public/blueskyyy.png") no-repeat center center fixed;
  background-size: cover;
  background-color: #f0f4f8;
  overflow-x: hidden;
  overflow-y: auto;
}

/* Headings in Roboto */
h1, h2, h3, h4, h5, h6 {
  @apply font-roboto font-semibold;
}

/* Hide scrollbar but keep scroll functionality */
.scrollbar-none::-webkit-scrollbar {
  display: none;
}
.scrollbar-none {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
